<template>
<a class="card-link" :href="link">
  <article class="blog-card">
    <img class="post-image" :src="imgsrc" />
    <div class="article-details">
      <h3 class="post-title">{{ title }}</h3>
      <p class="post-description">{{ detail }}</p>
    </div>
  </article>
</a>
</template>

<script>
export default {
  name: "Card",
  props:   {
          title: String,
          detail: String,
          link: String,
          imgsrc: String,
  }
}
</script>
<style>
.post-title{
  margin-top: 1em;
}
.blog-card{
  margin-top: 2em;
  margin-bottom: 1em;
}
.blog-card>img{
  max-width: 30em;
}
</style>